<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">收入-认购服务费</span>
				</div>
			</div>
			<div class="cash_flow_table table">
				<el-table ref="multipleTable" :data="datalistTotal" border style="width: 100%" empty-text="">
					<el-table-column prop="coin" label="币种">
						<template slot-scope="scope">
							{{ scope.row.unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<el-table-column prop="date" label="日期" width="200">
						<template slot-scope="scope">
							<el-date-picker v-model="scope.row.date" @change="getEventCountData($event,scope.$index)" :disabled="scope.$index != currentIndexTotal" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" :editable='false'>
							</el-date-picker>
						</template>
					</el-table-column>
					<el-table-column prop="event" label="事件" width="240">
						<template slot-scope="scope">
							{{ scope.row.event }}
						</template>
					</el-table-column>
					<el-table-column prop="type" label="类型" width="150">
						<template slot-scope="scope">
							收入-认购服务费
						</template>
					</el-table-column>
					<el-table-column prop="service_fee" label="应缴服务费（万元）" width="150">
					</el-table-column>
					<el-table-column prop="paid_service_fee" label="实缴服务费（万元）" width="150">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<el-button v-if="scope.$index != currentIndexTotal" @click="editTotal(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
							<el-button v-if="scope.$index == currentIndexTotal" @click="editSaveTotal(scope.row)" type="text" size="small">保存</el-button>
							<el-button v-if="scope.$index == currentIndexTotal" @click="editCancelTotal(scope.row)" type="text" size="small">取消</el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</div>
		
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">明细</span>
				</div>
			</div>
			<div class="cash_flow_table">
				<div class="table">     
					<el-table ref="multipleTable" :data="datalistDetail" border style="width: 100%" empty-text="">
						<el-table-column type="index" label="序号" width="100" sortable>
						</el-table-column>
						<el-table-column prop="investor_name" label="投资人" width="130" sortable>
						</el-table-column>
						<el-table-column prop="subscribe" label="认缴金额（万元）" width="150" sortable>
						</el-table-column>
						<el-table-column prop="service_fee" label="应缴服务费（万元）" width="150" sortable>
						</el-table-column>
						<el-table-column prop="pay" label="实缴服务费（万元）" width="150" sortable>
						</el-table-column>
						<el-table-column prop="date" label="到账日期" width="150" sortable>
						</el-table-column>
						<el-table-column prop="unit" label="币种" width="150" sortable>
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						
						<el-table-column fixed="right" label="操作" width="150">
							<template slot-scope="scope">
								<el-button @click="edit(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
							</template>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
			</div>
		</div>
		
		
		<!-- 弹出框模块开始 -->
		<el-dialog custom-class="pop" append-to-body title="编辑" :visible.sync="dialogVisible" width="700px" center>
			<div class="dialog_body table">
				<el-table :data="editData" border style="width: 100%" empty-text="">
					<el-table-column type="index" label="序号" width="70" sortable>
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								{{ scope.row.id }}
							</div>
							<div class="" v-else>
								新增
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="investor_name" label="当期实缴金额（万元）" width="170">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="currentIndexDetail == scope.$index">
									<el-input v-model="edit_pay" placeholder="请输入内容" clearable maxlength='16'>
									</el-input>
								</div>
								<div class="" v-else>
									{{ scope.row.pay }}
								</div>
							</div>
							<div class="" v-else>
								<el-input v-model="pay" placeholder="请输入内容" clearable maxlength='16'>
								</el-input>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="date" label="实际到账日期" width="240">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="currentIndexDetail == scope.$index">
									<el-date-picker v-model="edit_date" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" :editable='false'>
    									</el-date-picker>
								</div>
								<div class="" v-else>
									{{ scope.row.date }}
								</div>
							</div>
							<div class="" v-else>
								<el-date-picker v-model="date" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" :editable='false'>
    								</el-date-picker>
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="note" label="备注" width="150">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="currentIndexDetail == scope.$index">
									<el-input v-model="edit_note" placeholder="请输入内容" clearable>
									</el-input>
								</div>
								<div class="" v-else>
									{{ scope.row.note }}
								</div>
							</div>
							<div class="" v-else>
								<el-input v-model="note" placeholder="请输入内容" clearable>
								</el-input>
							</div>
						</template>
					</el-table-column>
					
					<el-table-column fixed="right" label="操作" width="130">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<div class="" v-if="scope.$index != currentIndexDetail">
									<el-button @click="editDetail(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
									<el-button @click="deleteDetail(scope.$index,scope.row)" type="text" size="small">删除</el-button>
								</div>
								<div class="" v-else>
									<el-button @click="editSaveDetail(scope.row)" type="text" size="small">保存</el-button>
									<el-button @click="editCancelDetail(scope.row)" type="text" size="small">取消</el-button>
								</div>
							</div>
							<div class="" v-else>
								<el-button @click="addSaveDetail(scope.row)" type="text" size="small">保存</el-button>
								<el-button @click="addClearDetail(scope.row)" type="text" size="small">清除</el-button>
							</div>
						</template>
					</el-table-column>
				</el-table>
			</div>
			<span slot="footer" class="dialog-footer">
			    <el-button @click="dialogVisible = false">取 消</el-button>
			    <el-button type="primary" @click="dialogVisible = false">关闭</el-button>
			</span>
		</el-dialog>
		<!-- 弹出框模块结束 -->
	</div>
</template>

<script>
	import { getCompanyRunCashIncomeServiceApi,editSaveTotalCompanyRunCashIncomeServiceApi,addSaveDetailCompanyRunCashIncomeServiceApi,getPayHistoryCompanyRunCashIncomeServiceApi,editSavePayHistoryCompanyRunCashIncomeServiceApi,deletePayHistoryCompanyRunCashIncomeServiceApi,getEventByDateApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn ,isValidMoneyLength,isValidDecimal,event_type_company,cash_type_income_subscribe} from '../../config/mUtils'
	
	export default {
		inject: ['reload'],
		
		data() {
			return {
				id: '',
				count: '',
				currentIndexTotal: -1,
				currentIndexDetail: -1,
				datalistTotal: [
					{
						unit: '',
						type: '收入-认购服务费',
						event: '',
					}
				],
				datalistDetail: [ ],
				editData: [ ],
				addData: {
					id: 0,
					unit: 3,
				},
				editListItem: {},//列表点击的数据
				// 弹出框内编辑字段开始
				pay: '',
				date: '',
				note: '',
				edit_pay: '',
				edit_date: '',
				edit_note: '',
				// 弹出框内编辑字段结束
				dialogVisible: false,
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.count = this.$route.query.count;
			this.getInitData()
		},
		computed: {
	
		},
		mounted() {
			
		},
		methods: {
			getInitData(){
				getCompanyRunCashIncomeServiceApi({
					sg_company_manager_id: this.id,
					count: this.count
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.$set(this.datalistTotal,0,res.data.payload.data.event);
						this.datalistDetail = res.data.payload.data.detail;
						for (var i = 0,max = this.datalistDetail.length;i<max;i++) {
							this.datalistDetail[i]['unit'] = res.data.payload.data.event.unit;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editTotal(index,e){
				this.currentIndexTotal = index;
			},
			editSaveTotal(e){
				editSaveTotalCompanyRunCashIncomeServiceApi({
					event: e.event,
					date: e.date,
					id: e.id,
					event_count: e.event_count
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.reload()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editCancelTotal(e){
				this.reload()
			},
			edit(index,e){
				console.log(e);
				this.editListItem = e;
				this.getPayHistoryList()
			},
			addSaveDetail(e){
				console.log(e);
				// return false;
				if(!this.pay){
					dialogConfirm("info", '当期实缴金额不能为空');
					return false;
				}
				
				if(!this.date){
					dialogConfirm("info", '实际到账日期不能为空');
					return false;
				}
				if(!isValidDecimal(this.pay)){
					dialogConfirm('info','当期实缴金额格式错误!');
					return false;
				}
				if(isValidMoneyLength(this.pay)){
					dialogConfirm('info','当期实缴金额过大!');
					return false;
				}
				addSaveDetailCompanyRunCashIncomeServiceApi({
					pay: this.pay,
					date: this.date,
					note: this.note,
					unit: this.editListItem.unit,
					investor_id: this.editListItem.investor_id,
					sg_company_manager_id: this.id,
					count: this.count,
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.clearData()
						this.getInitData()
						this.getPayHistoryList()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getPayHistoryList(){
				getPayHistoryCompanyRunCashIncomeServiceApi({
					investor_id: this.editListItem.investor_id,
					sg_company_manager_id: this.id,
					count: this.count,
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.dialogVisible = true;
						this.editData = res.data.payload.data;
						this.editData.unshift(this.addData);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addClearDetail(e){
				console.log(e);
				this.clearData()
			},
			editDetail(index,e){
				console.log(e);
				this.currentIndexDetail = index;
				this.edit_pay = e.pay;
				this.edit_date = e.date;
				this.edit_note = e.note;
			},
			editSaveDetail(e){
				// console.log(e);
				// return false;
				if(!this.edit_pay){
					dialogConfirm("info", '当期实缴金额不能为空');
					return false;
				}
				
				if(!this.edit_date){
					dialogConfirm("info", '实际到账日期不能为空');
					return false;
				}
				if(!isValidDecimal(this.edit_pay)){
					dialogConfirm('info','当期实缴金额格式错误!');
					return false;
				}
				if(isValidMoneyLength(this.edit_pay)){
					dialogConfirm('info','当期实缴金额过大!');
					return false;
				}
				editSavePayHistoryCompanyRunCashIncomeServiceApi({
					pay: this.edit_pay,
					date: this.edit_date,
					note: this.edit_note,
					id: e.id
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.currentIndexDetail = -1;
						this.clearData()
						this.getPayHistoryList()
						this.getInitData()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editCancelDetail(e){
				this.clearData()
				this.currentIndexDetail = -1;
			},
			deleteDetail(index,e){
				dialogConfirmHasbtn('确定要删除所选基金吗?').then(() => {
					deletePayHistoryCompanyRunCashIncomeServiceApi({
						id: e.id
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.getPayHistoryList()
							this.getInitData()
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}).catch(() => {});
			},
			clearData(){
				this.pay = '';
				this.date = '';
				this.note = '';
				this.edit_pay = '';
				this.edit_date = '';
				this.edit_note = '';
			},
			// 获取事件字段里的count值
			getEventCountData(date,index){
				if (!date) {
					return
				}
				if (this.datalistTotal[index].fund_type == 1) {
					var fund_type = 2;
				} else if(this.datalistTotal[index].fund_type == 2){
					var fund_type = 3;
				} else {
					var fund_type = '';
				}
				var event_id = this.datalistTotal[index].id;
				getEventByDateApi({
					type: event_type_company,
					id: this.id,
					cash_type: cash_type_income_subscribe,
					date: date,
					fund_type: fund_type,
					fund_id: this.datalistTotal[index].fund_id,
					event_id: event_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.datalistTotal[index].event = res.data.payload.event_name;
						this.datalistTotal[index]['event_count'] = res.data.payload.event_count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		/*padding: 0 30px 30px 30px;*/
	}
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	.company_name_right .delete {
		margin-right: 60px;
		color: #6292F4;
		font-size: 14px;
	}
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
	.submit_btn_box{
		margin-top: 35px;
	}
	.submit_btn_box button{
		height: 36px;
		margin: 0 10px;
		line-height: 36px;
		padding: 0 25px;
		color: #6292F4;
		font-size: 14px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
</style>


